import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { ListService } from "./services/list.service";
import swal from "sweetalert2";

@Component({
  selector: "app-list",
  templateUrl: "./list.component.html",
  styleUrls: ["./list.component.scss"]
})
export class ListComponent implements OnInit {
  listList;

  constructor(public listservice: ListService, private router: Router) {
    this.listList = [];
  }

  ngOnInit() {
    this.listservice.getList().subscribe(
      (data: any) => {
        this.listList = data;
        console.log(data);
      },
      error => {
        console.log(error);
      }
    );
  }

  delete(id) {
    swal
      .fire({
        title: "Do you want to delete the Applicant?",
        text: "You won't be able to revert it back",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Delete"
      })
      .then(result => {
        if (result.value) {
          this.listservice.deleteList(id).subscribe(
            (data: any) => {
              console.log("Deleted successfully");
              swal.fire("Deleted!", "Applicant Deleted.", "success");
              this.router.navigate(["/list"]);
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Deleted!", "There was some error.", "error");
              console.log("Error", error);
            }
          );
        }
      });
  }
}
