import { Component, OnInit } from "@angular/core";

import { ListService } from "../services/list.service";
import { ActivatedRoute, Router } from "@angular/router";
import swal from "sweetalert2";
import { List } from "app/shared/models/list.model";

@Component({
  selector: "app-edit-list",
  templateUrl: "./edit-list.component.html",
  styleUrls: ["./edit-list.component.scss"]
})
export class EditListComponent implements OnInit {
  updatedList;
  listid;

  constructor(
    public listservice: ListService,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {
    this.updatedList = new List({});
    this.listid = this.activeRouter.snapshot.params["id"];
  }

  ngOnInit() {
    this.listservice.getListById(this.listid).subscribe(
      (data: any) => {
        console.log("data", data);
        this.updatedList = data;
      },
      error => {
        console.log("error-->", error);
      }
    );
  }

  edit() {
    swal
      .fire({
        title: "Do you want to update the List?",
        text: "You won't be able to revert it back",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Update"
      })
      .then(result => {
        if (result.value) {
          this.listservice.editList(this.updatedList, this.listid).subscribe(
            (data: any) => {
              console.log("list added successfully");
              swal.fire("Updated!", "List Updated.", "success").then(() => {
                this.router.navigate(["list"]);
              });
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Updated!", "There was some error.", "error");
              console.log("Error", error);
            }
          );
        }
      });

    console.log("Submitted>>>>>>>>>", this.updatedList);
  }
}
