import { Injectable } from "@angular/core";
import { BaseService } from "../../../shared/services/base.service";
import { HttpClient } from "@angular/common/http";
import { List } from "app/shared/models/list.model";

@Injectable()
export class ListService extends BaseService {
  constructor(public http: HttpClient) {
    super("list");
  }

  getList() {
    return this.http.get(this.url);
  }
  addList(data) {
    return this.http.post(this.url + "add/", data, this.getOptions());
  }

  getListById(id) {
    return this.http.get(this.url + "edit/" + id, this.getOptions());
  }
  editList(updatedData: List, id) {
    return this.http.put(
      this.url + "edit/" + id,
      updatedData,
      this.getOptions()
    );
  }
  deleteList(id) {
    return this.http.delete(this.url + "delete/" + id, this.getOptions());
  }
}
