import { Component, OnInit } from "@angular/core";
import { ListService } from "../services/list.service";
import { Router } from "@angular/router";
import { List } from "app/shared/models/list.model";

import swal from "sweetalert2";

@Component({
  selector: "app-add-list",
  templateUrl: "./add-list.component.html",
  styleUrls: ["./add-list.component.scss"]
})
export class AddListComponent implements OnInit {
  newlist: List;
  submitting: boolean;

  constructor(public listsservice: ListService, public router: Router) {
    this.newlist = new List({});
  }

  ngOnInit() {}

  submit() {
    console.log("Submitted>>>>>>>>>", this.newlist);

    swal
      .fire({
        title: "Do you want to add the list ?",
        text: "New list will be added in List List",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Add"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.listsservice.addList(this.newlist).subscribe(
            (data: any) => {
              swal.fire("Add!", "List has been added.", "success");
              //dev
              console.log("Dataaa changeddd", data);
            },
            error => {
              swal.fire("Not Added!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
